import * as core from '../../core';
import { Point } from '../../core/math';

/**
 * PXScene renderer plugin for mesh
 *
 * @class
 * @memberof PIXI
 * @extends PIXI.ObjectRenderer
 */
export default class PXSceneMeshRenderer extends core.ObjectRenderer
{

    /**
     * constructor for renderer
     *
     * @param {PXSceneRenderer} renderer The renderer this mesh awesomeness works for.
     */
    constructor(renderer)
    {
        super(renderer);
        this._resourceLoaded = false;
    }

    /**
     * clone array
     *
     * @param {Array} arr the js array
     * @return {Array} the new array
     */
    cloneArray(arr)
    {
        if (!arr || arr.length <= 0)
        {
            return null;
        }

        const retArray = [];

        for (const i in arr)
        {
            retArray.push(arr[i]);
        }

        return retArray;
    }

    /**
     * renders mesh
     *
     * @param {PIXI.mesh.Mesh} mesh mesh instance
     */
    render(mesh)
    {
        const texture = mesh._texture;
        const parentRenderedObject = (mesh.parent && mesh.parent.renderedObject) || this.renderer.context;
        const point = new Point(mesh.x, mesh.y);
        const globalPoint = mesh.parent.toGlobal(point);

        if (!mesh.renderedObject)
        {
            mesh.renderedObject = this.renderer.view.create({
                t: 'image',
                type: 'mesh',
                parent: parentRenderedObject,
                a: 0,
            });
        }

        if (mesh.imageResource && mesh.renderedObject && this._resourceLoaded)
        {
            innerRenderMesh.call(this);
        }
        else if (!mesh.imageResource)
        {
            const imageUrl = texture.baseTexture.source.src;

            mesh.imageResource = this.renderer.view.create({
                t: 'imageResource',
                url: imageUrl,
            });

            mesh.imageResource.ready.then((imgResource) =>
            {
                texture.baseTexture.source = {
                    width: imgResource.w,
                    height: imgResource.h,
                    src: imageUrl,
                };
                texture.baseTexture._sourceLoaded();
                texture.baseTexture.emit('loaded', texture.baseTexture);
                mesh._onTextureUpdate();
                mesh.renderedObject.resource = imgResource;
                this._resourceLoaded = true;
                innerRenderMesh.call(this);
            });
        }

        function innerRenderMesh()
        {
            const renderedObject = mesh.renderedObject;
            const attributes = this._calcMeshAttributes(globalPoint, mesh.width, mesh.height, mesh);

            renderedObject.uvs = this.cloneArray(mesh.uvs);
            renderedObject.vertices = this.cloneArray(mesh.vertices);

            renderedObject.x = attributes.x;
            renderedObject.y = attributes.y;
            renderedObject.a = attributes.a;
            renderedObject.r = attributes.r;
            renderedObject.sx = attributes.sx;
            renderedObject.sy = attributes.sy;
        }
    }

    /**
     * Calculate mesh attribute.
     *
     * @param {Point} globalPoint - Global Point of the mesh
     * @param {Number} w - The width of the mesh
     * @param {Number} h - The height of the mesh
     * @param {Object} mesh - The mesh object
     * @return {Object} The calculated attributes.
     * @private
     */
    _calcMeshAttributes(globalPoint, w, h, mesh)
    {
        const x = globalPoint.x;
        const y = globalPoint.y;
        const a = mesh.alpha;
        const r = mesh.rotation * (180 / Math.PI);
        const sx = mesh.scale.x;
        const sy = mesh.scale.y;

        return {
            x,
            y,
            a,
            r,
            sx,
            sy,
        };
    }
}

core.PXSceneRenderer.registerPlugin('mesh', PXSceneMeshRenderer);
